// Bootstrap overrides

// Color system

$white:                           #fff;
$gray-light:                      #f2f2f2;
$gray-100:                        #E7E7E7; //#f0f3f5
$gray-200:                        #D8D8D8; //#c2cfd6
$gray-300:                        #B3B3B3; //#a4b7c1
$gray-400:                        #9C9C9C; //#869fac
$gray-500:                        #8C8C8C; //#678898
$gray-600:                        #6D6C6C; //#536c79
$gray-700:                        #555555; //#3e515b
$gray-800:                        #333333; //#151b1e
$gray-900:                        #2D2D2D; //#2936d3
$black:                           #000 !default;
$navy-black:                      #393e44;

$primary:                         #04b5b0;
$blue:                            #0A7595;
$light-blue:                      #e0f3f9;
$sky-blue:                        #f2f6f9;
$indigo:                          #6610f2 !default;
$purple:                          #965CA6 !default;
$pink:                            #e83e8c !default;
$red:                             #bb2d24;
$orange:                          #e17a10;
$yellow:                          #ffc107;
$green:                           #32932f;
$navy:                            #1B566A;
$dark-blue:                       #34476e;
$teal:                            #04b5b0;
$cyan:                            #0a7595;
$transparent:                     transparent;
$dominion:                        $teal; // For some legacy stuff. Use color(dominion) for new
$secondary:                       $gray-200;
$text-white:                      $white;
$text-black:                      $black;
$text-muted:                      $gray-400;
$print-text-muted:                $gray-500;
$button-bg-disabled:              #f2f2f2;
$light-disabled:                  #CCCCCC;
$dark-disabled:                   #999999;
$danger-background:               #fef3f3;
$disabled-card-background:        #fafafa;
$table-header:                    #59626D;
$dusty-teal:                      #88d0ce;
$jb-teal-bg:                      #f0f8f8;
$dusty-blue:                      #91cee8;
$jb-blue-bg:                      #ecf1f3;
$dusty-green:                     #9ed6a8;
$jb-green-bg:                     #e9f1eb;
$danger-border:                   #e4a0a0;
$wrapper:                         #393e44;
$default-blue:                     #0A7595;

$colors: (
    'blue':                           $blue,
    'indigo':                         $indigo,
    'purple':                         $purple,
    'pink':                           $pink,
    'red':                            $red,
    'orange':                         $orange,
    'yellow':                         $yellow,
    'green':                          $green,
    'teal':                           $teal,
    'cyan':                           $cyan,
    'white':                          $white,
    'gray':                           $gray-600,
    'gray-dark':                      $gray-800,
    'dominion':                       $teal,
    'secondary':                      $secondary
);

$theme-colors: (

  'primary':                        $teal,
  'secondary':                      $secondary,
  'success':                        $green,
  'info':                           $cyan,
  'warning':                        $orange,
  'danger':                         $red,
  'light':                          $gray-100,
  'gray':                           $gray-300,
  'medium':                         $gray-400,
  'medium-dark':                    $gray-600,
  'dark':                           $gray-800,
  'super-dark-gray':                $gray-900,
  'yellow':                         $yellow,
  'green':                          $green,
  'purple':                         $purple,
  'navy':                           $dark-blue,
  'default-blue':                   $default-blue
);

$status-colors: (
  'success':                        $green,
  'info':                           $cyan,
  'warning':                        $orange,
  'danger':                         $red,
);

// can be cleaned up to use status-color map
$custom-control-colors: (
  'primary':                        $teal,
  'success':                        $green,
  'info':                           $cyan,
  'warning':                        $orange,
  'danger':                         $red,
);

$avatar-colors: (
  'dark-red': #B20906,
  'red': #E11D18,
  'orange': #F4511E,
  'yellow-orange': #FF8D00,
  'yellow': #FFBE07,
  'lime-green': #A2CB38,
  'green': #4CAF50,
  'dark-green': #248028,
  'teal': #017E88,
  'aqua': #25CADA,
  'sky-blue': #14A7E9,
  'blue': #1469BC,
  'navy': #1D3077,
  'black': #111111,
  'gray': #828282,
  'brown': #935438,
  'indigo': #5F39A3,
  'purple': #9C27B0,
  'pink': #F45EB9,
  'magenta': #EA4079,
  'default': #0A7595
);

// Text colors
$text-white:                      $white;
$text-black:                      $black;
$text-muted:                      $gray-400;
$print-text-muted:                $gray-500;
$text-disabled:                   $gray-300;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-transitions:        true;
$enable-rounded:            true;
$enable-shadows:            true;
$border-radius:            .15rem;

$spacer: 1rem !default;
$spacers: (
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: ($spacer * .75),
  4: $spacer,
  5: ($spacer * 1.25),
  6: ($spacer * 1.5),
);

$sizes: (
  10: 10%,
  15: 15%,
  20: 20%,
  25: 25%,
  30: 30%,
  35: 35%,
  40: 40%,
  50: 50%,
  60: 60%,
  70: 70%,
  75: 75%,
  100: 100%
);

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  2xl: 1500px
) !default;

$container-max-widths: (
  xs: 540px,
  sm: 720px,
  md: 960px,
  lg: 1140px,
  xl: 1400px,
  2xl: 1600px
) !default;

// Body
//
// Settings for the `<body>` element

$body-bg:                  #e4e5e6;
$body-color:               $gray-900;
$line-height-base:         1.5 !default;

// Links
//
//$link-color:             $gray-700;
$btn-link-color:           $gray-700;
$text-white-hover:         darken($white, 5%);
$link-decoration:          none;

// Typography
//
// Font, line-height, and color for body text, headings, and more.
$font-family-sans-serif:    'Source Sans Pro', sans-serif;
$font-family:               $font-family-sans-serif;
$font-size-base:            0.875rem;
$h1-font-size:              1.25rem !default;
$h2-font-size:              1.1rem !default;
$h3-font-size:              1rem !default;
$h4-font-size:              .95rem !default;
$paragraph-margin-bottom:   1rem;

//
//Transitions
//
$transition-base:                     all .2s ease-in-out;

// Alerts

$alert-padding-negative-x:       2.25rem;

// Avatar
$avatar:                        $text-muted;

// Border
$border-color:                  rgba(0,0,0,0.1); // Matches <hr/>

// Breadcrumbs

$breadcrumb-bg:                   #fff;
$breadcrumb-padding-top-y:        .75rem;
$breadcrumb-padding-bottom-y:     0;

// Alerts

$alert-padding-negative-x:       2.25rem;

// Cards

$card-border-radius:              .15rem;
$card-border-color:               #fff;
$card-bordered-color:             $gray-100;
$card-cap-bg:                     #fff;
$card-box-shadow:                 0px 2px 3px rgba(0,0,0,0.15);
$card-box-shadow-dark:            0px 3px 12px rgba(0,0,0,0.35);
$card-box-shadow-hover:           0px 5px 6px rgba(0,0,0,0.15);

$card-spacer-y:                   .75rem;
$card-spacer-x:                   1.25rem;
$card-spacer-x-sm:                1rem;
$card-header-spacer-y:            1rem;
$card-header-spacer-button-y:     .75rem;

// Content Wrapper
$content-padding-top:             16px;
$content-padding-right:           80px;
$content-padding-bottom:          16px;
$content-padding-left:            48px;

// Dropdowns

$dropdown-padding-y:              0;
$dropdown-border-color:           $gray-200;
$dropdown-divider-bg:             $gray-100;
$dropdown-link-active-color:      inherit;
$dropdown-link-active-bg:         inherit;
$dropdown-focus-bg:               #F5F5F5;
$dropdown-link-hover-bg:          $dropdown-focus-bg;
$dropdown-box-shadow:             0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
$dropdown-item-padding-y:         0;
$dropdown-item-padding-x:         16px;

// Forms

$input-minheight:                 35px;
$input-color:                     $gray-700;
$input-border-radius:             .15rem;
$input-box-shadow:                 0 0 0 transparent;
$input-focus-shadow:               0 0 5px rgba(4, 181, 176, 0.43);
$input-focus-danger-box-shadow:    0 0 0 3px rgba($red,.15);

$input-btn-focus-box-shadow: 0 0 5px rgba(4, 181, 176, 0.43);
$input-btn-focus-box-shadow-success: 0 0 5px rgba(68, 181, 89, .43);
$input-btn-focus-box-shadow-info: 0 0 5px rgba(76, 167, 207, .43);
$input-btn-focus-box-shadow-warning: 0 0 5px rgba(240, 172, 79, .43);
$input-btn-focus-box-shadow-danger: 0 0 5px rgba(215, 93, 93, .43);


$input-btn-padding-y-sm:           .25rem !default;
$input-btn-padding-x-sm:           .5rem !default;
$input-btn-line-height-sm:         1.5 !default;

$custom-control-indicator-bg:     $white;
$custom-control-indicator-border: $dominion;
$custom-control-indicator-box-shadow: inset 0 0 0 rgba($black,.0);
$custom-control-indicator-active-bg: $dominion;
$custom-control-indicator-focus-box-shadow: none;

$custom-checkbox-indicator-icon-checked: none;
$custom-radio-indicator-icon-checked: none;

$custom-select-focus-border-color: $teal;
$custom-select-focus-box-shadow:   $input-focus-shadow;

// Disabled Form Elements
$input-disabled-bg:                    $gray-100;
$custom-select-disabled-color:         $gray-700;
$custom-select-disabled-bg:            $gray-100;
$custom-control-label-disabled-color:  $dark-disabled;
$custom-control-indicator-disabled-bg: $light-disabled;
$custom-control-indicator-checked-disabled-bg: $dark-disabled;
$custom-control-indicator-checked-disabled-bg: $dark-disabled;


$form-group-xl:                     2rem;

//Breadcrumbs

$breadcrumb-padding-y:          .75rem !default;
$breadcrumb-padding-x:          0;
$breadcrumb-item-padding:       .5rem !default;

$breadcrumb-bg:                 transparent;
$breadcrumb-divider-color:      $gray-300;
$breadcrumb-active-color:       $gray-600 !default;
$breadcrumb-divider:            "/" !default;

// Buttons

$btn-link-secondary:              $gray-500;
$btn-secondary-border:            $gray-300;
$btn-box-shadow:                  0 0 0 transparent;
$btn-pill-radius:                 2rem;
$btn-list-padding:                .5rem .25rem;
$btn-icon-padding:                .375rem;
$btn-icon-focus-bg:               $dropdown-focus-bg;
$btn-icon-table-focus-bg:         #e4e1b6;
$btn-icon-table-active-bg:        #cdcaa1;
$btn-icon-ripple-bg:              #f5f5f5;
$btn-focus-width:                 2px;
$input-btn-focus-width:           0 0 5px;
$input-btn-focus-box-shadow:      0 0 5px rgba(4, 181, 176, 0.43);

$input-btn-focus-box-shadow-width: 0 0 0 $btn-focus-width;
$input-btn-focus-box-shadow-teal: rgba(4, 181, 176, 0.43);
$input-btn-focus-box-shadow-success:rgba(68, 181, 89, .43);
$input-btn-focus-box-shadow-info:  rgba(76, 167, 207, .43);
$input-btn-focus-box-shadow-warning: rgba(240, 172, 79, .43);
$input-btn-focus-box-shadow-danger: rgba(215, 93, 93, .43);

$btn-focus-box-shadow:            $input-btn-focus-box-shadow-width $input-btn-focus-box-shadow-teal;
$btn-focus-bg:                    rgba(0,0,0,0.12);
$button-group-color:              #808080;

// Disabled Buttons
$button-border-disabled:          1px solid $light-disabled;

// Progress bars

$progress-bg:                     $gray-light;
$progress-box-shadow:             none;
$progress-height:                 14px;
$progress-sm-height:              7px;

// Tables

$table-header-color:              $table-header;
$table-header-text-color:         $white;
$table-header-padding:            .5rem;
$table-cell-padding:              .75rem .5rem;
$table-cell-padding-sm:           .4rem .5rem;
$table-cell-padding-sm-y:         .4rem;
$table-bordered-header-cell-padding: .4rem .5rem .8rem .5rem;
$bordered-table-header-border:    3px solid $table-header;
$table-bg-accent:                 $gray-100;
$table-bg-hover:                  $gray-100;
$table-border-color:              $gray-100;
$table-border-footer-color:       $gray-200;
$table-border-bottom:             1px solid $gray-200;
$disabled-table-bg:               $gray-light;
$table-valign:                    middle;
$table-border-color:              $gray-100;
$table-row-min-height:            60px;

// Calendar

$table-actions-bg:                  #393E44;
$table-actions-color:               #eee;
$cal-info-font-size:                0.75rem;
$table-hover:                       #fbf9e0;
$table-disabled-input-hover:        #e6e4cb;
$table-input-error-bg:              #fbe4e0;
$table-icon-hover:                  #e0deca;
$table-border-bottom:               1px solid $gray-200;
$table-info-col-bg:                 #f9f9f9;
$table-info-row-bg:                 #f9f9f9;//#f2f2f2;
$table-cell-padding-lg:             .75rem 1.25rem;

// Lists

$list-bordered-header-padding-top: .4rem;
$list-bordered-header-padding-bottom: .8rem;
$list-border:                     1px solid rgba(0,0,0,0.1);

// Forms

$input-group-addon-bg:            $transparent;
$input-border-color:              $gray-200;
$input-group-addon-border-color:  $gray-200;
$input-focus-border-color:        $teal;
$input-border-width:              1px;
$input-btn-padding-y:             .375rem !default;
$input-btn-padding-x:             .75rem !default;
$input-padding-y:                 $input-btn-padding-y !default;
$input-padding-x:                 $input-btn-padding-x !default;

// Badges

$badge-font-size:                 0.70rem; // Sits a bit nicer in the space
$badge-lg-font-size:              0.70rem; // Original BS4 value
$badge-text-transform:            uppercase;
$badge-pill-padding-x:            .65rem;
$badge-padding-lg-y:              0.48rem;
$badge-padding-lg-x:              2.1rem;

// Pagination

$pagination-color:                 $gray-300;
$pagination-border-color:          $gray-200;

$pagination-hover-color:           $dominion;
$pagination-hover-bg:              $transparent;
$pagination-hover-border-color:    $dominion;

// Print
$print-page-size:                   auto;

// Sidenav

$sidenav-border:                    10px solid $dominion;

// Dialogs and Modals
$modal-title-line-height:           $line-height-base;
$modal-border:                      $list-border;
$zindex-modal-backdrop:             990;
$zindex-modal:                      1000;
$modal-content-padding:             1rem;
$modal-inner-padding:               1rem;
$modal-header-padding:              0 0 1rem 0;
$modal-footer-padding:              1rem 0 0 0;

// Navigation
$nav-icon-color:                    #888b8e;
$nav-text-color:                    #dcdcdd;

// Transitions
$menu-text-opacity-transition:       opacity 1s ease-in-out;
$menu-text-color-transition:         color 0.2s ease-in-out;
$menu-background-color-transition:   background-color 0.2s ease-in;

// Object List container
$object-list-container-bg:          #f4f4f4;
$object-list-container-border:      1px solid #e1e1e1;
$object-list-container-header-padding: 0.75rem;
$object-list-container-body-padding: 0 0.75rem;

$table-border-color:                  #e7e7e7;
$table-box-shadow:                    0px 4px 4px rgba(0,0,0,0.25);
$table-sticky-border-width:           1px;
$table-sticky-border-color:           #e2e2e2;
$table-sticky-header-border-color:    #505050;
$table-sticky-hidden-header-border:   1px solid $table-header;
$table-sticky-hidden-border:          1px solid #fff;
$table-header-border:                 $table-sticky-border-width solid $table-sticky-header-border-color;
$table-border:                        $table-sticky-border-width solid $table-sticky-border-color;

// Custom scrollbar
$scrollbar-height:                    10px;
$scrollbar-border:                    1px solid #d5d5d5;
$scrollbar-border-radius:             10px;
$scrollbar-track-bg:                  #E7E7E7;
$scrollbar-thumb-bg:                  #B3B3B3;

// Tabs
$tab-margin:                          0 4%;
$tab-padding:                         1rem 1rem .5rem;
$tab-border-inactive:                 3px solid transparent;
$tab-border-active:                   3px solid $gray-900;
$tab-transition:                      $transition-base;

// Widgets
$widget-spacer-x:                      $card-spacer-x;
$widget-spacer-y:                      2.5rem;
